
@import './assets/css/common.css';
@import './assets/css/commonPc.css';
#app{
    width:100%;
    min-width:1280px;
    margin:0 auto;
    /* background: #F2F2F2; */
}
.fade-enter-active {
    position:relative;
    -webkit-transition:all 1s ease;
    transition: all 1s ease;
    left:0;
}
.fade-enter{
  	left:100%;
}
.fade-leave{
    left:-100%;
}
.fade2-enter-active {
    position:relative;
    -webkit-transition:all 1s ease;
    transition: all 1s ease;
    left:0;
}
.fade2-enter{
  	left:-100%;
}
.fade2-leave{
    left:100%;
}




